$(document).ready(function() {
    sliderMain();
    sliderNews();
    sliderAbout();
    sliderTours();
    sliderSeaCrimea();
    sliderSeaKrasnodar();
    sliderSeaAbkhazia();
    sliderCatalog();

    // Hover-Click
    $(".header-bottom__item").hover(onIn, onOut);
    $(".header-bottom__item .header-bottom__item-show").click(function() {
        if ($(this).parents(".header-bottom__item").hasClass("header-bottom__item--active")) {
            $(this).parents(".header-bottom__item").removeClass("header-bottom__item--active")
        } else {
            $(this).parents(".header-bottom__item").addClass("header-bottom__item--active")
        }
    })

    // Burger
    $('.header-top__burger').click(function () {
        $(this).toggleClass('header-top__burger--active')
        $('.header-bottom__inner').toggleClass('header-bottom__inner--active')
        $('body').toggleClass('fixed-body')
    })

    // Filter
    $('.catalog__filter').click(function () {
        $(this).addClass('catalog__filter--active')
        $('.catalog__left-wrapp').addClass('catalog__left-wrapp--active')
        $('.header').css('z-index','1')
        $('body').addClass('fixed-body')
    })

    $('.catalog__left-close').click(function () {
        $('.catalog__filter').removeClass('catalog__filter--active')
        $('.catalog__left-wrapp').removeClass('catalog__left-wrapp--active')
        $('.header').css('z-index','2')
        $('body').removeClass('fixed-body')
    })

    // Tabs
    $(".tours__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tours__tab").removeClass("tours__tab--active")
        $(this).addClass("tours__tab--active")

        $(this).parents(".tours__inner").find(".tours__content").removeClass("tours__content--active")
        $(this).parents(".tours__inner").find(`.tours__content[data-content-path="${path}"]`).addClass("tours__content--active")
    })



    // Range
    const rangeInput = document.querySelectorAll('.range-input input')
    priceInput = document.querySelectorAll('.price-input input')
    progress = document.querySelector('.slider .progress')

    let priceGap = 10000

    priceInput.forEach(input =>{
        input.addEventListener("input", e => {

            let minVal = parseInt(priceInput[0].value),
                maxVal = parseInt(priceInput[1].value)

            if ((maxVal - minVal >= priceGap) && maxVal <= 250000) {
                if (e.target.className === 'input-min') {
                    rangeInput[0].value = minVal;
                    progress.style.left = (minVal / rangeInput[0].max) * 100 + '%'
                } else {
                    rangeInput[1].value = maxVal;
                    progress.style.right = 100 - (maxVal / rangeInput[1].max) * 100 + '%'
                }
            }
        })
    })

    rangeInput.forEach(input =>{
        input.addEventListener("input", e => {

            let minVal = parseInt(rangeInput[0].value),
                maxVal = parseInt(rangeInput[1].value)

            if (maxVal - minVal < priceGap) {
                if (e.target.className === 'range-min') {
                    rangeInput[0].value = maxVal - priceGap;
                } else {
                    rangeInput[1].value = minVal + priceGap
                }
            } else {
                priceInput[0].value = minVal;
                priceInput[1].value = maxVal;
                progress.style.left = (minVal / rangeInput[0].max) * 100 + '%'
                progress.style.right = 100 - (maxVal / rangeInput[1].max) * 100 + '%'
            }
        })
    })
    $('.input-min').add()

})

function onIn() {
    if (window.innerWidth > 991) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-bottom__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 991) {
        $(this).removeClass("header-bottom__item--active")
    }
}